import Vue from 'vue'
import App from './App.vue'
import Vuelidate from 'vuelidate'
import VueScrollTo from 'vue-scrollto'
import store from './store'
import './assets/style.scss'



Vue.use(VueScrollTo)

Vue.config.productionTip = false
Vue.use(Vuelidate)
new Vue({
  store,
  render: h => h(App)
}).$mount('#app')
