<template>
    <section class="sectionUsers">
      <h1>Our cheerful users</h1>
      <p>Attention! Sorting users by registration date</p>
      <div class="userGrid">

        <div
          v-bind:class="[post.class, post.classHide]"
           v-for="(post,id) of posts" :key="id"
        >
          <img v-bind:src="post.photo" alt="">
          <h2 class="userName" >{{post.name}}</h2>
          <p  class="userPosition" >{{post.position}}</p>
          <p  class="userInfo">{{post.email}}</p>
          <p  class="userInfo">{{post.phone}}</p>
        </div>

      </div>
      <button type="button" name="button" v-on:click="showAll">Show more</button>
    </section>
</template>




<script>
export default {
name: 'Users',
  data(){
    return{
      posts:['']
    }
  },
  props: ['newUserData'],

  async created(){
    var res = await fetch("https://frontend-test-assignment-api.abz.agency/api/v1/users?page=1");
    var poste = await res.json();
    this.posts = poste.users;
    for (var i = 0; i < this.posts.length; i++) {
      this.posts[i].class = "userContent";
   }

    if (document.documentElement.clientWidth < 425){
      for (var b = 3; b < this.posts.length; b++) {
         this.posts[b].classHide = "userHide";
        }
    }else{
      for (var a = 5; a < this.posts.length; a++) {
         this.posts[a].classHide = "userHide";
       }
    }
  },

methods:{
 showAll: function (){
    alert(document.documentElement.clientWidth);
     for (var q = 0; q < this.posts.length; q++) {
          this.posts[q].classHide = "userShow";
     }
    }
   }
}
</script>
















<style lang="scss">
@import '@/assets/style.scss';

  .sectionUsers{
    background: $background-second-color;
    height: 1105px;
    width: 100%;
    @include flexbox(flex, center, center, column);
    h1{
      margin-bottom: 20px;
    }
  }

  .userGrid{
    display: grid;
    grid-template-columns: 200px 200px 200px;
    grid-template-rows: repeat(auto);
    margin-top: 60px;
    grid-gap: 30px;
    grid-row-gap: 60px;
    grid-column-gap: 130px;
    margin-bottom: 60px;
  }


  .userContent{
     text-align: center;
     img{
       width: 70px;
       height: 70px;
       border-radius: 50%;
     }
     .userName{
       margin-top: 20px;
     }
     .userPosition{
       margin-top: 21px;
     }
     .userInfo{
       margin-top: 12px;
     }
  }

  .userHide{
    display: block;
  }

  .userShow{
    display: block;
  }




  @media screen and (max-width: 768px){
    .sectionUsers{
      height: 1030px;
      justify-content: flex-start;
      padding-top: 120px;
    }

    .userGrid{
      width: 95%;
      grid-template-columns: 32% 32% 32%;
      grid-template-rows: auto auto auto;
      margin-top: 60px;
      grid-gap: 15px;
      grid-row-gap: 90px;
      margin-bottom: 0;
    }

}


  @media screen and (max-width: 425px){
    .sectionUsers{
      height: auto;
      padding: 50px 0px;
      h1{
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }
    .userGrid{
      grid-template-columns: 100%;
      grid-template-rows: repeat(auto);
      margin: 0 auto;
      margin-top: 27px;
      grid-gap: 15px;
      grid-row-gap: 50px;
      margin-bottom: 40px;
    }
    .userContent{
       .userName{
         margin-top: 15px;
       }
       .userPosition{
         margin-top: 10px;
       }
       .userInfo{
         margin-top: 10px;
       }
    }
    .userHide{
      display: none;
    }

      .userShow{
        display: block;
      }
  }
</style>
