<template>
  <section class="sectionRegistration" id="element">

    <h1>Register to get a work</h1>
    <p>Attention! After successful registration and alert, update the list of users in the block from the top</p>

    <form class="" action="index.html" method="post" @submit.prevent="submitHandler">
       <label for="userName">Name</label>
       <input
          type="text"
          name=""
          v-model.trim="$v.name.$model"
          :class="{invalid: ($v.name.$dirty && !$v.name.required) || ($v.name.$dirty && !$v.name.minLength)}"
          placeholder="Your name"
          class="userValu"
          id="userName"
        >
        <small
          v-if="$v.name.$dirty && !$v.name.required"
          class="invalid"
          >Enter your name
        </small>
        <small
          v-if="$v.name.$dirty && !$v.name.minLength"
          class="invalid"
          >Minimum 2 characters
        </small>
       <label for="userEmail">Email</label>
       <input
           type="email"
           name=""
           v-model.trim="$v.email.$model"
           :class="{invalid: ($v.email.$dirty && !$v.email.required) || ($v.email.$dirty && !$v.email.email)}"
           placeholder="Your email"
           class="userValu"
           id="userEmail"
        >
        <small
          v-if="$v.email.$dirty && !$v.email.required"
          class="invalid"
          >Enter your email
        </small>
        <small
          v-if="$v.email.$dirty && !$v.email.email"
          class="invalid"
          >Incorrect email
        </small>
       <label for="userPhone">Phone</label>
       <input
           type="number"
           name=""
           v-model.trim="$v.phone.$model"
           :class="{invalid: ($v.phone.$dirty && !$v.phone.required) || ($v.phone.$dirty && !$v.phone.minLength)}"
           placeholder="+380 XX XXX XX XX"
           class="userValu"
           id="userPhone"
        >
        <small
          v-if="$v.phone.$dirty && !$v.phone.required"
          class="invalid"
          >Enter your phone
        </small>
        <small
          v-if="$v.phone.$dirty && !$v.phone.minLength"
          class="invalid"
          >Need more than {{-phone.length + 8}}
        </small>
       <p class="userPrompt">Еnter phone number in open format</p>

       <div class="userRadioBox">
         <p>Select your position</p>
         <label><input type="radio" name="position" v-model="picked" value="1" checked>Frontend developer</label>
         <label><input type="radio" name="position" v-model="picked" value="2" >Backend developer</label>
         <label><input type="radio" name="position" v-model="picked" value="3" >Designer</label>
         <label><input type="radio" name="position" v-model="picked" value="4">QA</label>
       </div>
       <div class="form-controlGroup">
         <label class="form-label" for="file">Photo</label>
         <div class="form-controlGroup-inputWrapper">
           <label class="form-input form-input--file">
              <div class="form-input--file-text">Upload your photo</div>
              <span class="form-input--file-button">Browse</span>
              <input
              class="form-input-file"
              type="file"
              id="file"
              accept="image/*"
              size="14"
              @change="onFileChange"
               />
           </label>
         </div>
       </div>
       <small
         class="invalid"
         v-if="!image"
         >File not found
       </small>
       <div v-else>
         <img :src="image" />
       </div>

       <button type="submit" name="button">Sing up now</button>
    </form>


  <transition name="popUpAnimation">
    <div
    class="successfulRegistration"
    v-show='finReg'
    >
    <div class="popUpRegistr">
      <div class="popUpRegistrInside">
        <h2>Congratulations</h2>
        <img src="@/assets/Shape.png" alt="" @click="finReg = !finReg">
      </div>
      <hr>
      <p>You have successfully passed the registration</p>
      <hr>
      <div class="popUpRegistrInside">
        <div class=""></div>
        <button type="button" name="button" @click="finReg = !finReg">Great</button>
      </div>
     </div>
    </div>
  </transition>

  </section>
</template>




<script>

import {email, required, minLength} from 'vuelidate/lib/validators'

export default {
  name: 'registration',
  data(){
    return{
      name:'',
      email: '',
      phone: '',
      image: '',
      picked: '1',
      finReg: false,
      newUser: Object
    }
  },
  validations:{
    name:{required, minLength: minLength(2)},
    email: {email, required},
    phone: {required , minLength: minLength(8)},
  },
  methods:{
    //Валидация формы
    submitHandler(){
      if (this.$v.$invalid || this.image == '') {
        this.$v.$touch()
        return;
      }

      //Сбор нового пользователя
      const objectUser = {
        username: this.name,
        useremail: this.email,
        userphone: this.phone,
        userposition: this.picked,
        userPhoto: this.image
      }

      var formData = new FormData();
// file from input type='file'
formData.append('position_id', this.picked,);
formData.append('name', this.name);
formData.append('email', this.email);
formData.append('phone', this.phone);
formData.append('photo', this.image);

fetch('https://frontend-test-assignment-api.abz.agency/api/v1/users', {
  method: 'POST',
  body: formData,
  headers: {
    'Token': 'eyJpdiI6Im9mV1NTMlFZQTlJeWlLQ3liVks1MGc9PSIsInZhbHVlIjoiRTJBbUR4dHp1dWJ3ekQ4bG85WVZya3ZpRGlMQ0g5ZHk4M05UNUY4Rmd3eFM3czc2UDRBR0E4SDR5WXlVTG5DUDdSRTJTMU1KQ2lUQmVZYXZZOHJJUVE9PSIsIm1hYyI6ImE5YmNiODljZjMzMTdmMDc4NjEwN2RjZTVkNzBmMWI0ZDQyN2YzODI5YjQxMzE4MWY0MmY0ZTQ1OGY4NTkyNWQifQ==',
    "success": true,
  },
})
.then(function(response) {
  return response.json();
})
.then(function(data) {
  console.log(data);
  if(data.success) {
    // process success response
  } else {
    // proccess server errors
  }
})



      this.$emit('submitHandler', {
        objectUser
   })

      //Появления модального окна
       this.finReg = true;
    },

    //Рендеринг загруженого изображения
    onFileChange(e) {
        var files = e.target.files || e.dataTransfer.files;
        if (!files.length)
          return;
        this.createImage(files[0]);
      },
      createImage(file) {
        var reader = new FileReader();
        var vm = this;

        reader.onload = (e) => {
          vm.image = e.target.result;
        };
        reader.readAsDataURL(file);
      }
  },

  mounted(){
    //Работа Input type file
    var fileInput = document.querySelector('.form-input-file');
    var fileInputText = document.querySelector('.form-input--file-text');
    var fileInputTextContent = fileInputText.textContent;

    fileInput.addEventListener('change', function(e) {
     var value = e.target.value.length > 0 ? e.target.value : fileInputTextContent;
     fileInputText.textContent = value.replace('C:\\fakepath\\', '');
});
  }
}
</script>

<style lang="scss">
@import '@/assets/style.scss';

.sectionRegistration{
  height: 1070px;
  width: 100%;
  @include flexbox(flex, center, flex-start, column);
  padding-top: 160px;
  h1{
    margin-bottom: 20px;
  }
  p{
    width: 467px;
    text-align: center;
  }
}

form{
  @include flexbox(flex, flex-start, center, column);
  width: 467px;
  margin-top: 0px;

  label{
    margin-top: 22px;
    color: #212529;
  }
  .userValu{
    width: 100%;
    height: 38px;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    padding-left: 10px;
    margin-top: 10px;
    margin-bottom: 2px;
    transition: all 0.4s ease;
    font-size: 16px;
