  }
  .invalid{
    border-color: #D25A66;
    color: #D25A66;
    display: block;
  }
  .userPrompt{
    font-size: 12px;
    text-align: left;
  }
  p{
    color: #212529;
  }
  button{
    margin: 0 auto;
    margin-top: 16px;
    margin-bottom: 42px;
  }
  small{
    display: none;
  }
  .userPhoto{
    margin: 22px;
  }
  img {
    width: 25%;
    height: auto;
    margin: auto;
    display: block;
    margin-bottom: 10px;
    margin-top: 10px;
    border: 2px solid #54DD35;
    border-radius: 4px;
  }
}



.userRadioBox{
  @include flexbox(flex, flex-start, center, column);
  margin-bottom: 20px;
  p{
    margin: 10px 0px;
    text-align: left;
  }
  label{
    margin: 4px 0px;
    color: #212529;
  }
  input{
    margin-right: 12px;
  }
}

input:active{
border: 1px solid #80BDFF;
}


/*
  Custom file input
 */

 .form-controlGroup{
    width: 467px;
 }
 .form-controlGroup-inputWrapper{
   width: 100%;
   height: 38px;
   margin: 5px 0px;
 }


.form-input--file {
	position: relative;
	overflow: hidden;
}

.form-input--file-text{
  width: 100%;
  height: 38px;
  border: 1px solid #CED4DA;
  border-radius: 4px;
  color: #B2B9C0;
  padding-left: 10px;
  @include flexbox(flex, center, flex-start, row);
}

.form-input-file {
	opacity: 0;
	visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
}

.form-input--file-button {
	cursor: pointer;
	position: absolute;
	top: 0;
	right: -467px;
	padding: .44em 1em 0;
  height: 38px;
  border: 1px solid #CED4DA;
	background-color: #F8F7F5;
  border-radius: 0px 4px 4px 0px;
  color: #212529;
  transition: all 0.5s ease;
	&:hover {
		background-color: #CED4DA;
	}
}

/*

*/

.successfulRegistration{
  width: 100%;
  height: 100vh;
  background: rgb(1,1,1,0.9);
  position: fixed;
  top: 0;
  @include flexbox(flex, center, center, row);

}

.popUpRegistr{
  width: 400px;
