.popUpAnimation-enter-active, .popUpAnimation-leave-active {
  transition: all .3s ease;
}
.popUpAnimation-enter{
  transform: scale(0.5);
  opacity: 0;
}
.popUpAnimation-leave-to{
  transform: scale(0.5);
  opacity: 0;
}


  @media screen and (max-width: 768px){
    .sectionRegistration{
      height: 970px;
      padding-top: 110px;
      p{
        width: 445px;
      }
    }

