      @include flexbox(flex, flex-start, center, column);
      width: 445px;
      margin-top: 0px;
    }


     .form-controlGroup{
        width: 445px;
     }

   .form-input--file-button {
     right: -445px;
   }
}


  @media screen and (max-width: 425px){
    .sectionRegistration{
      height: 940px;
      p{
        width: 90%;
      }
      h1{
        margin-bottom: 15px;
      }
    }

    form{
      @include flexbox(flex, flex-start, center, column);
      width: 90%;
      margin-top: 15px;
      margin-top: 0px;
    }

    .form-controlGroup{
       width: 100%;
    }

   .form-input--file-button {
     right: -90vw;
   }
   .popUpRegistr{
     width: 90%;
     height: auto;
   }

  }


</style>
